import React from "react";
import { useState } from "react";
import Dice from "../../components/organisms/yahtzee/Dice";
import { Repeat } from "typescript-tuple";
import { Paper, Button, Tooltip } from "@material-ui/core";
import GameButton from "../../components/organisms/GameButton";

import "../../style/yahtzee/Yahtzee.css";
import HowToModal from "../../components/organisms/HowToModal";

/*
12
  
  1
  
  2
  
  3
  
  
  
  
*/

// 
type DiceState = {
  roll: number;
  keeped: boolean;
};

// 
type ScoreState = {
  value: number | undefined;
  used: boolean;
};

type AllScoreState = Repeat<ScoreState, 12>;
type BoardState = Repeat<DiceState, 5>;

// 
type GameState = {
  readonly scores: Repeat<AllScoreState, 2>;
  readonly dices: BoardState;
  readonly stepNumber: number;
  readonly rotateNumber: number;
};

const Game = () => {
  const [state, setState] = useState<GameState>({
    scores: [
      [
        { value: undefined, used: false },
        { value: undefined, used: false },
        { value: undefined, used: false },
        { value: undefined, used: false },
        { value: undefined, used: false },
        { value: undefined, used: false },
        { value: undefined, used: false },
        { value: undefined, used: false },
        { value: undefined, used: false },
        { value: undefined, used: false },
        { value: undefined, used: false },
        { value: undefined, used: false },
      ],
      [
        { value: undefined, used: false },
        { value: undefined, used: false },
        { value: undefined, used: false },
        { value: undefined, used: false },
        { value: undefined, used: false },
        { value: undefined, used: false },
        { value: undefined, used: false },
        { value: undefined, used: false },
        { value: undefined, used: false },
        { value: undefined, used: false },
        { value: undefined, used: false },
        { value: undefined, used: false },
      ],
    ],
    dices: [
      {
        roll: 0,
        keeped: false,
      },
      {
        roll: 0,
        keeped: false,
      },
      {
        roll: 0,
        keeped: false,
      },
      {
        roll: 0,
        keeped: false,
      },
      {
        roll: 0,
        keeped: false,
      },
    ],
    stepNumber: 0,
    rotateNumber: 0,
  });
  // 
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const handName = [
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "S",
    "B",
    "",
  ];

  const handIcon = [
    "icon-ace",
    "icon-deuce",
    "icon-tray",
    "icon-four",
    "icon-five",
    "icon-six",
    "icon-choice",
    "icon-fourdice",
    "icon-fullhouse",
    "icon-shortstrate",
    "icon-bigstrate",
    "icon-yahtzee",
  ];

  const handDetail = [
    "1",
    "2",
    "3",
    "4",
    "5",
    "6",
    "",
    "4\n",
    "23\n",
    "4\n15",
    "5\n30",
    "5\n50",
  ];

  const renderTooltip = (text: string, detail: string, icon?: string) => {
    return (
      <Tooltip
        title={<span style={{ fontSize: "0.8rem" }}>{detail}</span>}
        placement="right-end"
      >
        <th>
          <div className="hand-name-cell">
            <div className={icon}></div>
            <span>{text}</span>
          </div>
        </th>
      </Tooltip>
    );
  };

  const renderHandName = (i: number) => {
    return renderTooltip(handName[i], handDetail[i], handIcon[i]);
  };

  const renderHandScore = (player_num: number, i: number) => {
    // 
    const setScore = () => {
      // 
      if (state.scores[player_num][i].value === undefined) {
        return;
      }
      // 
      if (state.scores[player_num][i].used) {
        return;
      }
      setState(({ scores, dices, stepNumber, rotateNumber }) => {
        // 
        const newScores = scores.slice() as Repeat<AllScoreState, 2>;
        newScores[player_num][i].used = true;
        for (let i = 0; i < 12; i++) {
          if (newScores[player_num][i].used) continue;
          newScores[player_num][i].value = undefined;
        }

        // 
        const newDices = dices.slice() as BoardState;
        for (let i = 0; i < 5; i++) {
          newDices[i].keeped = false;
        }

        if (stepNumber === 23) {
          if (calcTotalScore(0) > calcTotalScore(1)) {
            window.alert("player1!!!");
          } else if (calcTotalScore(0) < calcTotalScore(1)) {
            window.alert("player2!!!");
          } else {
            window.alert("!!!");
          }
        }

        return {
          scores: newScores,
          dices: newDices,
          stepNumber: stepNumber + 1,
          rotateNumber: 0,
        };
      });
    };
    return (
      <td
        className={
          state.stepNumber % 2 === player_num && state.stepNumber !== 24
            ? "score-table-cell"
            : ""
        }
        style={{
          opacity: state.scores[player_num][i].used ? 1.0 : 0.7,
        }}
      >
        <Button
          variant="text"
          onClick={setScore}
          style={{
            padding: "0px",
            fontSize: "1rem",
          }}
        >
          {state.scores[player_num][i].value !== undefined
            ? state.scores[player_num][i].value
            : "-"}
        </Button>
      </td>
    );
  };

  const renderHand = (i: number) => {
    return (
      <tr>
        {renderHandName(i)}
        {renderHandScore(0, i)}
        {renderHandScore(1, i)}
      </tr>
    );
  };

  const renderSubtotalScore = (player_num: number) => {
    return (
      <td>
        <Button
          disabled
          style={{
            padding: "0px",
            fontSize: "1rem",
          }}
        >
          <span
            style={{
              color: "rgba(0, 0, 0, 1)",
            }}
          >
            {bonus(player_num)}/63
          </span>
        </Button>
      </td>
    );
  };

  const renderSubtotal = () => {
    return (
      <tr>
        <th>
          <div className="hand-name-cell">
            <span></span>
          </div>
        </th>
        {renderSubtotalScore(0)}
        {renderSubtotalScore(1)}
      </tr>
    );
  };

  const renderBonusScore = (player_num: number) => {
    return (
      <td>
        <Button
          disabled
          style={{
            padding: "0px",
            fontSize: "1rem",
          }}
        >
          <span
            style={{
              color: "rgba(0, 0, 0, 1)",
            }}
          >
            {bonus(player_num) >= 63 ? "+35" : "+0"}
          </span>
        </Button>
      </td>
    );
  };

  const renderBonus = () => {
    return (
      <tr>
        <th>
          <div className="hand-name-cell">
            <span>35</span>
          </div>
        </th>
        {renderBonusScore(0)}
        {renderBonusScore(1)}
      </tr>
    );
  };

  const bonus = (player_num: number): number => {
    let bonusScoreSum: number = 0;
    for (let i = 0; i < 6; i++) {
      if (state.scores[player_num][i].used) {
        bonusScoreSum += state.scores[player_num][i].value as number;
      }
    }
    return bonusScoreSum;
  };

  const calcTotalScore = (player_num: number) => {
    let total_score: number = 0;
    // 
    for (let i = 0; i < 12; i++) {
      total_score += state.scores[player_num][i].used
        ? (state.scores[player_num][i].value as number)
        : 0;
    }
    // 35
    if (bonus(player_num) >= 63) {
      total_score += 35;
    }
    return total_score;
  };

  const renderTotalScore = (player_num: number) => {
    return (
      <td
        className={
          state.stepNumber % 2 === player_num && state.stepNumber !== 24
            ? "score-table-cell"
            : ""
        }
      >
        <span className="total-score-label">{calcTotalScore(player_num)}</span>
      </td>
    );
  };

  const renderTotal = () => {
    return (
      <tr>
        <th scope="row"></th>
        {renderTotalScore(0)}
        {renderTotalScore(1)}
      </tr>
    );
  };

  const renderDice = (i: number) => {
    const setKeeped = () => {
      setState(({ scores, dices, stepNumber, rotateNumber }) => {
        const newDices = dices.slice() as BoardState;
        if (rotateNumber > 0) {
          newDices[i].keeped = !newDices[i].keeped;
        }
        return {
          scores: scores,
          dices: newDices,
          stepNumber: stepNumber,
          rotateNumber: rotateNumber,
        };
      });
    };

    return (
      <Button
        variant="text"
        className="dice-button"
        onClick={setKeeped}
        style={{ padding: "20px" }}
      >
        <Dice
          roll={state.dices[i].roll}
          rotated={state.rotateNumber}
          keeped={state.dices[i].keeped}
        />
      </Button>
    );
  };

  // 
  const rotate = () => {
    if (state.rotateNumber === 3) {
      window.alert("3");
      return;
    }
    // 

    setState(({ scores, dices, stepNumber, rotateNumber }) => {
      const newDices = dices.slice() as BoardState;
      for (let i = 0; i < 5; i++) {
        if (newDices[i].keeped) {
          continue;
        }
        newDices[i].roll = Math.floor(Math.random() * 6);
      }
      return {
        scores: scores,
        dices: newDices,
        stepNumber: stepNumber,
        rotateNumber: rotateNumber + 1,
      };
    });
    setTimeout(() => {
      console.log(state.stepNumber);
      setState(({ scores, dices, stepNumber, rotateNumber }) => {
        const newScores = scores.slice() as Repeat<AllScoreState, 2>;
        for (let i = 0; i < 12; i++) {
          if (newScores[stepNumber % 2][i].used) continue;
          newScores[stepNumber % 2][i].value = calc_score(i, dices);
        }
        return {
          scores: newScores,
          dices: dices,
          stepNumber: stepNumber,
          rotateNumber: rotateNumber,
        };
      });
    }, 3100);
  };

  return (
    <div className="game-wrapper">
      <Paper elevation={1}>
        <div className="score-table-wrapper">
          <table className="score-table">
            <tbody>
              <tr>
                <th>
                  {Math.min(12, Math.floor(state.stepNumber / 2) + 1)}/12
                </th>
                <th>player1</th>
                <th>player2</th>
              </tr>
              <tr>
                <th></th>
                <th></th>
                <th></th>
              </tr>
              {renderHand(0)}
              {renderHand(1)}
              {renderHand(2)}
              {renderHand(3)}
              {renderHand(4)}
              {renderHand(5)}
              {renderSubtotal()}
              {renderBonus()}
              <tr>
                <td colSpan={3}>
                  <span className="bonus-text">
                    <div className="icon-ace"></div>
                    <div className="icon-six"></div>
                    63
                  </span>
                </td>
              </tr>
              {renderHand(6)}
              {renderHand(7)}
              {renderHand(8)}
              {renderHand(9)}
              {renderHand(10)}
              {renderHand(11)}
              {renderTotal()}
            </tbody>
          </table>
        </div>
      </Paper>
      <div className="board">
        <div className="dice-wrapper">
          {renderDice(0)}
          {renderDice(1)}
          {renderDice(2)}
          {renderDice(3)}
          {renderDice(4)}
        </div>
        <div>
          <div className="rotate-text">{3 - state.rotateNumber}</div>
          <GameButton title={""} onClick={rotate} />
        </div>
      </div>
      <HowToModal
        open={open}
        handleOpen={handleOpen}
        handleClose={handleClose}
        content={
          <div>
            <h2></h2>
            <p>
              
              
              
            </p>
            <p>
              
              
              
            </p>
            <p>
              
              
            </p>
            <p>
              
              
            </p>
          </div>
        }
      />
    </div>
  );
};

// 
const calc_score = (index: number, dice: BoardState) => {
  let score = 0;
  // 
  let cnt_roll: number[] = new Array(6).fill(0);
  let sum_roll: number = 0;
  for (let i = 0; i < 5; i++) {
    cnt_roll[dice[i].roll]++;
    sum_roll += dice[i].roll + 1;
  }

  if (index < 6) {
    // 
    score = cnt_roll[index] * (index + 1);
  } else if (index === 6) {
    // 
    score = sum_roll;
  } else if (index === 7) {
    // 
    for (let i = 0; i < 6; i++) {
      if (cnt_roll[i] >= 4) {
        score = sum_roll;
      }
    }
  } else if (index === 8) {
    // 
    let fh_flag: number = 1;
    for (let i = 0; i < 6; i++) {
      if (cnt_roll[i] === 5) {
        fh_flag *= 6;
      } else if (cnt_roll[i] === 3) {
        fh_flag *= 3;
      } else if (cnt_roll[i] === 2) {
        fh_flag *= 2;
      }
    }
    if (fh_flag === 6) {
      score = sum_roll;
    }
  } else if (index === 9) {
    // 
    for (let i = 0; i < 3; i++) {
      let ss_flag: number = 0;
      for (let j = i; j < i + 4; j++) {
        if (cnt_roll[j] >= 1) {
          ss_flag++;
        }
      }
      if (ss_flag === 4) {
        score = 15;
      }
    }
  } else if (index === 10) {
    // 
    for (let i = 0; i < 2; i++) {
      let ss_flag: number = 0;
      for (let j = i; j < i + 5; j++) {
        if (cnt_roll[j] >= 1) {
          ss_flag++;
        }
      }
      if (ss_flag === 5) {
        score = 30;
      }
    }
  } else if (index === 11) {
    for (let i = 0; i < 6; i++) {
      if (cnt_roll[i] === 5) {
        score = 50;
      }
    }
  }
  return score;
};

const Yahtzee = () => {
  return <Game />;
};

export default Yahtzee;
